import { Component, ElementRef, OnDestroy, OnInit, ViewChild, ViewContainerRef } from '@angular/core';
import { StoreAbstract } from '../../../../core/classes/store.abstract';
import { ListComponent } from '../../../chatflow/components/atom/list/list.component';

@Component({
  selector: 'app-chat-history',
  templateUrl: './chat-history.component.html',
  styleUrl: './chat-history.component.scss'
})
export class ChatHistoryComponent extends StoreAbstract implements OnInit, OnDestroy {

  @ViewChild('container', { read: ViewContainerRef, static: true })
  public container!: ViewContainerRef;
  @ViewChild('scrollBody', { read: ElementRef, static: true })
  scrollBody!: ElementRef;

  ngOnInit() {
    this.globalService.liskovChatflow.layoutManager.find('chathistorypage')?.prepare(this.container);
    //this.globalService.chatflow.scenario('history');
    
    this.dalService.chatHistoryHttp.get(this.currentUser.userData.userIdx).subscribe(response => {
      if (response == null) return;
      
      this.historyService.allhistoryList = JSON.parse(response.data);
      const groupHistoryList = this.historyService.groupHistoryList();
      this.dynamicLayoutService.historyListContainer = this.factoryService.createComponent(
        ListComponent,
        this.container,
        groupHistoryList
      );
      //this.dynamicLayoutService.historyListContainer.instance.scrollBody.nativeElement.scrollTop = this.dynamicLayoutService.historyListContainer.instance.scrollBody.nativeElement.scrollHeight;
    });
  }

  ngOnDestroy() {
    this.globalService.liskovChatflow.layoutManager.find('chathistorypage').clear();
  }

}
